body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.header{
  padding: 20px 70px;
  background-color: black;
  color: white;
  display: flex;
  justify-content: space-between;
}
.title{
  color: white;
  padding-left: 3vw;
  font-weight: bolder;
  font-size: 25px;
}
.cart-icon .counter{
  
  
  height: 18px;
  width:18px ;
  background-color: blue;
  border-radius: 50%;
  position: relative;
  right: 7px;
  top: -6px;
 
}
.counter{
  display: flex;
  text-align: center;
  justify-content: center;
  font-size: small;
}
.cart-icon{
  display: flex;
 
}
.icon{
  position: relative;
  right: 2px;
  font-size: 22px;
}
img{
  height: 150px;
}
.card-wrapper{
  display: inline-flex;
  margin: 40px;
  width: 320px;
  box-shadow: 0px 0px 7px black;
  border-radius: 5px;
}
.image-wrapper{
  width: 180px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  border-right: 1px solid rgb(201, 201, 201);
}
.content{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
  font-size: larger;
}
.price{
  margin-bottom: auto;
}
.content button {
  background-color: black;
  color: white;
  border-radius: 3px;
}
.quantity {
  display: flex;
  align-self: flex-end;
  border: 1px solid grey;
  border-radius: 3px;
}
.quantity div {
  height: 22px;
  width: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
  
}


.quantity >button,
.quantity > div:last-child {
  background-color: rgb(0, 0, 111);
  color: white;
  
}

.quantity > button :disabled {

 opacity: 0.7;
  
}
main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap :50px
}
.footer{
  background-color: grey;
  box-sizing: border-box;
  height: 50px;
  font-weight: bolder;
  width: 100%;
  padding: 17px 75px;
  position: fixed;
  bottom: 0;
  text-align: right;
}
